@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Colors: 

#141b1f, rgb(20, 27, 31) - dark,
rgba(23, 162, 184, 1)- blue
*/

/*------ Layout ------*/

#root {
  font-family: 'Montserrat', 'Arial', sans-serif;
  background-color: rgb(20, 27, 31);
}

/*------ Link styles ------ */

.link-style-hash {
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
}

.link-style, .link-style:hover, .link-style-hash:hover {
  color: white;
}

.rotate-animation {
  border: none;
}

.rotate-animation:hover {
  -webkit-animation: rotate 1.5s;
          animation: rotate 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.underscore, .underscore-sections {
  position: relative;
}

.underscore::after, .underscore-sections::after {
  content: '';
  background-color: rgba(23, 162, 184, 1);
  position: absolute;
  bottom: -3px;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transition: -webkit-transform ease-in-out 250ms;
  transition: -webkit-transform ease-in-out 250ms;
  transition: transform ease-in-out 250ms;
  transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms;
}

.underscore-sections::after {
  height: 1px;
}

.underscore:hover::after, .underscore-sections:hover::after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  10% {
    -webkit-transform: rotateY(30deg);
            transform: rotateY(30deg);
  }
  20% {
    -webkit-transform: rotateY(60deg);
            transform: rotateY(60deg);
  }
  30% {
    -webkit-transform: rotateY(80deg);
            transform: rotateY(80deg);
  }
  40% {
    -webkit-transform: rotateY(120deg);
            transform: rotateY(120deg);
  }
  50% {
    -webkit-transform: rotateY(160deg);
            transform: rotateY(160deg);
  }
  60% {
    -webkit-transform: rotateY(200deg);
            transform: rotateY(200deg);
  }
  70% {
    -webkit-transform: rotateY(240deg);
            transform: rotateY(240deg);
  }
  80% {
    -webkit-transform: rotateY(300deg);
            transform: rotateY(300deg);
  }
  90% {
    -webkit-transform: rotateY(340deg);
            transform: rotateY(340deg);
  }
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  10% {
    -webkit-transform: rotateY(30deg);
            transform: rotateY(30deg);
  }
  20% {
    -webkit-transform: rotateY(60deg);
            transform: rotateY(60deg);
  }
  30% {
    -webkit-transform: rotateY(80deg);
            transform: rotateY(80deg);
  }
  40% {
    -webkit-transform: rotateY(120deg);
            transform: rotateY(120deg);
  }
  50% {
    -webkit-transform: rotateY(160deg);
            transform: rotateY(160deg);
  }
  60% {
    -webkit-transform: rotateY(200deg);
            transform: rotateY(200deg);
  }
  70% {
    -webkit-transform: rotateY(240deg);
            transform: rotateY(240deg);
  }
  80% {
    -webkit-transform: rotateY(300deg);
            transform: rotateY(300deg);
  }
  90% {
    -webkit-transform: rotateY(340deg);
            transform: rotateY(340deg);
  }
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

/* ------ Titles h1, h2, h3,... ------ */


#home h2, #portfolio h2, #blog h2 {
  background-color: rgba(0, 0, 0, 0.8);
}

#home h2, #blog h2{
  height: 100px;
}

#portfolio h2  {
  height: 70px;
}

/*------ Navigation------*/

#nav-bar {
  background-color: #141b1f;
}

.navbar-collapse {
  background-color: #141b1f;
}

svg.active-item {
  color: rgba(23, 162, 184, 1);
}

.active-item::before {
  content: '';
  background-color: rgba(23, 162, 184, 1);
  position: absolute;
  top: -5px;
  left: 45%;
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
}

div.breadcrumb {
  background-color: rgba(0, 0, 0, 0.7);
  margin: 0;
  height: 100%;
}

span.active.breadcrumb-item::before {
  color: white;
}

#hash-list-toggler {
  background-color: #141b1f;
  border: 2px dotted gray;
  width: 40px;
  height: 40px;
}

/*------ Social icons ------ */

.social-icon-item {
  font-size: 30px;
}

/*------ Section ------*/

/*------ Home page ------ */

#home .h2-row {
  height: 100px;
  background-attachment: fixed;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}

/*---- Hero Image section ----*/

#hero-image {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4))), url(/my-website/static/media/hero_image.f0fb1496.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/my-website/static/media/hero_image.f0fb1496.jpg);
  height: calc(100vh - 72px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

